import React from "react";
import { Message } from "semantic-ui-react";
interface Props {
    errors: any;
}
export default function ValidationErrors({ errors }: Props) {
    return (
        <Message error>
            {errors && (
                <Message.List>
                    {errors.map((err: any,i:any) => (//use i as index when no real key in maps
                        <Message.Item key={i}>
                                {err}
                        </Message.Item>
                    ))}
                </Message.List>
            )}
        </Message>
    )
}